@import url(https://fonts.googleapis.com/css2?family=Jost:wght@100;200;300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Jost:wght@100;200;300;400;500&display=swap);
* {
    font-family: 'Jost', sans-serif;
}

body {
    overflow-x: hidden;
}

.ad-container {
    background-color: rgb(32, 37, 75);
    width: 100vw;
    height: 40px;
    position: absolute;
    left: 0;
    top: 0;
    padding: 12px;
    overflow-x: hidden;
}

.ad-container > h1 {
    position: absolute;
    left: 0;
    font-size: 13px;
    color: aliceblue;
    white-space: nowrap;
    -webkit-animation-name: scroll;
            animation-name: scroll;
    -webkit-animation-duration: 30s;
            animation-duration: 30s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes scroll {
    0%{
        left: 100vw;
    }

    100%{
        left: -50vw;
    }
}

@keyframes scroll {
    0%{
        left: 100vw;
    }

    100%{
        left: -50vw;
    }
}

/* ------------------------- Navbar ------------------------- */

.nav-container {
    display: flex;
    justify-content: center;
    width: 100vw;
    height: 150px;
    margin-top: 40px;
}

#searchContainer {
    display: none;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid black;
    width: 100vw;
    height: 100px;
    margin-top: 40px;
    margin-bottom: 60px;
}

.nav-container>* {
    margin-top: 40px;
    position: absolute;
}

.shop-link {
    top: 25px;
    left: 5%;
    cursor: pointer;
    display: inline-block;
    font-size: 15px;
    width: 60px;
    height: 24px;
    color: rgb(133, 133, 133);
}

.brand {
    display: inline;
    font-size: 45px;
    font-weight: 600;
    top: 50px;
    margin: 0;
    cursor: default;
    text-decoration: none;
    color: black;
}

.icons {
    display: flex;
    justify-content: space-between;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    top: 28px;
    right: 5%;
}

.icons>button, #searchContainer>button {
    border: none;
    background-color: transparent;
    padding: 0;
}

.main_icons {
    color: black;
    font-size: 22px;
    margin: 0 0.7vw 0 0.7vw;
}

.main_icons:hover {
    font-size: 23px;
}

.searchInput {
    width: 400px;
    border-left: none;
    border-right: none;
    border-top: none;
    outline: none;
}

.x_icons {
    color: black;
    font-size: 17px;
    margin: 0 0.7vw 0 0.7vw;
}

.x_icons:hover {
    font-size: 18px;
}

@media only screen and (max-width: 500px) {
    .brand {
        font-size: 30px;
        top: 60px;
    }
    .icons {
        justify-content: space-around;
        right: 3%;
    }
}

.row {
    width: 100vw;
    background-color: rgb(32, 37, 75);
    height: 200px;
    padding-top: 80px;
    margin-left: 0 !important;
}

.copyright {
    font-weight: 300;
    font-size: 15px;
    margin-left: 40px;
    color: #fff;
}

.col-md-2>ul>li {
    display: flex;
    justify-content: space-around;
}

.col-md-2>h5 {
    font-weight: 600;
    font-size: medium;
    color: #fff;
}

/* ------------------------- Products ------------------------- */

.product_image {
    width: 30vw;
    display: inline;
    margin-left: 10px;
    margin-right: 10px;
    overflow: hidden;
}

.product_image>img {
    height: 30vw;
    width: inherit;
    object-fit: cover;
    margin-bottom: 1vw;
}

.alt_image {
    height: 27.5vw !important;
    margin-left: -30vw;
    opacity: 0;
    transition-duration: 300ms;
}

.alt_image:hover {
    opacity: 100%;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.clothes_name {
    font-size: 80%;
    margin: 0 0 10px 0;
}

.clothes_price {
    font-size: 90%;
    font-weight: 300;
    margin-bottom: 50px;
}


@media only screen and (max-width: 770px) {
    .product_image {
        width: 40vw;
    }

    .alt_image {
        height: 40vw !important;
        margin-left: 0;
        margin-top: -45vw;
    }

    .product_image>img {
        height: 40vw;
    }
}

/* ------------------------- Selected ------------------------- */

.selContainer  {
    display: grid;
    align-items: center;
    grid-template-columns: 35vw 80px 145px auto;
    height: 160px !important;
    grid-column-gap: 3vw;
    -webkit-column-gap: 3vw;
            column-gap: 3vw;
    grid-row-gap: 5px;
    row-gap: 5px;
    border: none;
  }

  .selQuantity>* {
    display: inline;
    font-size: 15px;
    border: none;
    background-color: transparent;
  }

  .selTitles {
    display: flex;
    align-content: center;
    justify-content: center;
  }
  
  .selPhoto  {
    height: 100px;
    width: 100px;
    display: inline;
    object-fit: cover;
  }
  
  .selName  {
    margin-left: 10px;
    display: inline-block;
    cursor: pointer;
    width: calc(35vw - 115px);
    height: 100px;
    align-items: center;
    word-wrap: break-word;
    overflow: hidden;
  }

  .selName:hover {
    text-decoration: underline;
}
  
  .selQuantity  {
    border: 2px groove;
    margin: 0 120px 0 0;
    width: 140px;
    height: 45px;
    display: flex;
    justify-content: space-around;
    align-items: center;
  }
  
  .selTotal, .selPrice {
    margin-left: 0;
    margin-right: 0;
    direction: rtl;
  }
  
  @media only screen and (max-width: 650px) {
    .selContainer  {
        grid-template-columns: 25% 25% 25% 25%;
        grid-template-rows: 50% 50%;
    }
    .selQuantity  {
        grid-column: 2 / 3;
    }
}
