.grid-container {
    display: grid;
    grid-template-columns: 35vw 80px 145px auto;
    grid-column-gap: 3vw;
    -webkit-column-gap: 3vw;
            column-gap: 3vw;
    grid-row-gap: 5px;
    row-gap: 5px;
    padding: 5px;
    margin: 0 7vw 0 7vw;
    font-size: 13px;
  }
  
  .grid-container > div {
    padding: 20px 0;
  }
  
  .item1 {
    font-size: 40px;
    grid-column-start: 1;
    grid-column-end: 3;
    padding: 0 !important;
  }

  .item2 {
    grid-column-start: 3;
    grid-column-end: 5;
    text-align: end;
    font-size: 16px;
  }

  .item3 {
    text-align: start;
    overflow: hidden;
  }

  .item4 {
    text-align: end;
    overflow: hidden;
  }

  .item5 {
    text-align: start;
    overflow: hidden;
  }

  .item6 {
    text-align: end;
    overflow: hidden;
  }

